.vsl {
  max-width: 800px;
  margin: 0 auto;
  position: relative;
  padding-top: 56.25%; // Atualize o padding-top para manter a proporção de 16:9
  height: 0;
  overflow: hidden; // Adicione esta linha para evitar overflow

  iframe,
  video {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    border-radius: 10px;
  }
}

.container{
  width: 100%;
  max-width: 1140px;
}
