
.gradient-pv2 {
  background: linear-gradient(to right, #3cc871, #11998e);
  height: 770px;
}

.btn-conversion{
  font-size: 26px;
  font-weight: 600;
  text-shadow: 0px 0px 10px rgba(23, 77, 0, 0.91);
  fill: #fff;
  color: #fff;
  background-color: transparent;
  background-image: linear-gradient(180deg, #ff6641 56%, #ff7925 100%);
  border-style: solid;
  border-width: 0 0 4px;
  border-color: #bd391f;
  border-radius: 15px;
  padding: 16px 20px;
  width: 90%;
  margin: 12px auto;
}
